.header {
    text-align: center;
    color:#fff;
    background: #333;
    padding: 10px;
}

.btn {
    display: inline-block;
    padding: 10px 5px;
    background: #333;
    color: #fff;
    border: none;
    cursor: pointer;
}

.btn:hover {
    background-color: #666;
}

.container {
    padding: 0 1rem;
}

.link {
    color: #fff;
    text-decoration: none;

}